.principles {
  position: relative;
  padding-bottom: var(--section-padding-lg);
  &__right {
    position: absolute;
    @include cover;
    top: calc(0px - var(--section-padding-lg));
    right: var(--section-padding);
    bottom: 0;
    max-width: 480px;
    @include flex(flex-end, normal, column); }
  &__right-block {
    background-color: $black-light;
    padding: 45px 30px;
    z-index: 1;
    position: relative; }

  &__right-title {
    margin-bottom: 15px; }
  &__right-word {
    position: absolute;
    bottom: 5%;
    left: 0;
    z-index: 0;
    @include bold;
    font-size: 160px;
    color: $red-lighten;
    @include rotate(-90);
    text-transform: uppercase;
    transform-origin: 10% 70%; } }


.principle {
  position: relative;
  @include notlast {
    margin-bottom: var(--section-padding-lg); }
  @include before {
    position: absolute;
    background-color: $white;
    height: 1px;
    width: 130px;
    top: 30px;
    right: calc(100% + 20px); }
  &:nth-child(1) {
    left: $left-5-1;
    max-width: 280px; }
  &:nth-child(2) {
    left: $left-5-2;
    max-width: 430px; }
  &:nth-child(3) {
    left: $left-5-3;
    max-width: 340px; }
  &__text {
    ul {}
    li {
      position: relative;
      &:before {
        content: '-';
        margin-right: 25px; } } }
  &__letter {
    position: absolute;
    margin-top: -.4em;
    top: 0;
    left: 0;
    z-index: 0;
    overflow: hidden;
    .text-stroke {
      position: relative;
      left: -.15em; } } }
